// GLOBALL STYLES
* {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  padding: .5vh 0;
}
a {
  text-decoration: none;
  font-size: .75rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #575A5C;
}
a:hover {
  color: #0069D2;
}

.active {
  display: flex !important;
  transition: 1s;
}
.inActive {
  display: none;
  transition: 1s;
}

.reveal {
  visibility: visible;
}
.hide {
  display: none !important;
}

@keyframes pulse {
  0% {
    box-shadow: 0px 1px 2px #2E3134;
  }
  50% {
    box-shadow: 5px 15px 20px #2e3134;
  }
  0% {
    box-shadow: 0px 1px 2px #2E3134;
  }
}

////////////////////////////////////////////////////////////////////////////////
.container {
  overflow: hidden;
  height: auto;
}

// HEADER STYLES

.navWrapper {
  display: flex;
  flex-direction: column;
  background: #fff; // remove on completion
  .mainMenu {
    height: 12vh;
  }
  // .hamburger, .hamburgerMenu {
  //   display: none;
  // }
  .topNav, .bottomNav {
    display: flex;
    background: #fff;
  }
  .topNav {
    border: 1px solid grey;
    height: 40%;
    .nav-link {
      display: flex;
      border-right: 1.25px solid grey;
      flex-grow: .05;
      justify-content: center;
      align-items: center;
      // background: teal;
    }
    .buffer {
      flex-grow: 2;
    }
  }
  .bottomNav {
    justify-content: center;
    height: 60%;
    // margin-top: -1px;
    border-left: 1.25px solid grey;
    border-right: 1.25px solid grey;
    .logoWrapper, .linksWrapper, .navBtn {
      align-items: center;
    }
    .logoWrapper, .navBtn {
      // align-self: center;
      // flex-grow: .25;
      width: 20%;
    }
    .logoWrapper {
      display: flex;
      justify-content: center;
      height: 100%;
      position: relative;
    }
    .logo {
      height: 5vh;
      width: 65%;
      background-image: url('./images/ifit-logo.svg');
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      bottom: 0;
      left: 2vw;
    }
    .linksWrapper {
      flex-grow: 2;
      display: flex;
      justify-content: center;
      width: 60%;
      .nav-link {
        padding: 1.5rem;
      }
    }
    .navBtn {
      display: flex;
      justify-content: center;
      button {
        color: #fff;
        padding: .5rem 1rem;
        margin-left: 10vw;
        background: #0069D2;
        border: none;
        border-radius: .15rem;
        font-size: .75rem;
        box-shadow: 2px 2px 10px #575A5C;
        text-transform: uppercase;
      }
    }
  }
}

////////////////////////////////////////////////////////////////////////////////

// HERO STYLES

.heroWrapper {
  height: 90vh;
  background-color: #000;
  background-image: url('./images/heroImage.png');
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  .title {
    position: relative;
    text-align: center;
    top: 30vh;
    color: #fff;
    font-size: 3rem;
  }
  button {
    width: 25%;
    color: #fff;
    padding: .75rem 1rem;
    background: #0069D2;
    border: none;
    border-radius: .15rem;
    font-size: .75rem;
    box-shadow: 2px 2px 10px #575A5C;
    text-transform: uppercase;
    align-self: center;
    position: relative;
    top: 35vh;
  }
}

