////////////////////////////////////////////////////////////////////////////////

// IFITCARDS STYLES

.fc_wrapper {
  height: 65vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50vh;
  .grabber {
    position: relative;
    top: 12vh;
    font-size: 1.25rem;
    font-weight: bold;
    color: #5D6063;
  }
  .fc_deck {
    height: 92%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    .card {
      height: 75%;
      width: 24%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-end;
      position: relative;
      bottom: -15vh;
      box-shadow: -2px 2px 4px #000;
      .treadmill, .bikes, .ellipticals, .strength {
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
        top: 2.5vh;
        height: 80%;
        width: 100%;
      }
      .treadmill {
        background-image: url('./images/treadmills.png');
      }
      .bikes {
        background-image: url('./images/bikes.png');
      }
      .ellipticals {
        background-image: url('./images/ellipticals.png');
      }
      .strength {
        background-image: url('./images/strength.png');
      }
      h2 {
        position: relative;
        margin: 0 auto;
        top: -2.5vh;
        color: #5D6063;
      }
    }
  }
}

////////////////////////////////////////////////////////////////////////////////

// FOOTER STYLES

footer {
  display: flex;
  flex-direction: column;
  // justify-content: center;
  align-items: center;
  height: 45vh;
  background: #2E3134;
  .footerMenu {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: left;
    width: 55%;
    color: #fff;
    .column {
      position: relative;
      top: 5vh;
      width: 5vw;
      margin: 0 5rem;
      li:first-of-type {
        font-weight: bold;
      }
      li {
        width: 10vw;
        &:hover {
          color: #0069D2;
        }
      }
    }
  }
  .socialBar_wrapper {
    height: 10vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 8vh;
    background: #2E3134;
    border-top: .5px solid #fff;
    border-bottom: .5px solid #fff;
    .social-link {
      height: 5vh;
      width: 5vh;
      border-radius: 50%;
      border: none;
      margin: 0 1.5rem;
      background: rgb(0,105,210);
      background: linear-gradient(150deg, rgba(0,105,210,1) 0%, rgba(40,225,165,1) 100%);
      display: flex;
      justify-content: center;
      align-items: center;
      .icon {
        color: #fff;
        height: 50%;
        width: 50%;
      }
      .pinterestIcon, .facebookIcon, .twitterIcon {
        background-repeat: no-repeat;
        background-size: cover;
        height: 50%;
        width: 50%;
      }
      .pinterestIcon {
        background-image: url('./images/pinterestIcon.svg');
      }
      .facebookIcon {
        background-image: url('./images/facebookIcon.svg');
      }
      .twitterIcon {
        background-image: url('./images/twitterIcon.svg');
        color: #fff;
      }
    }
  }
  .footerBottom {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 10vh;
    width: 100%;
    color: #fff;
    position: relative;
    bottom: -10vh;
    background: #2E3134;
    padding: 2vh 0;
    button {
      border: none;
      background: none;
      color: #fff;
      font-size: .75rem;
      flex-grow: 1;
      width: 10%;
      .icon {
        position: relative;
        top: .15vh;
        left: .5vw;
      }
    }
    .footerData {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-right: 10vw;
      width: 90%;
      p, a {
        margin: 0 1.5vw;
        &:hover {
          color: #0069D2;
        }
      }
    }
    .link {
      color: #fff;
      font-weight: normal;
    }
  }
}

////////////////////////////////////////////////////////////////////////////////

// MEDIA QUERIES

@media (min-width: 820px) {
  .container {
    .hamburger {
      position: absolute;
      margin-left: -50vw;
    }
  }
}

@media (max-width: 820px) {
  .container {
    .navWrapper {
      .topNav, .bottomNav {
        .nav-link, .navBtn > button {
          font-size: .5rem;
        }
      }
    }
    .cc_Wrapper {
      .cardDeck {
        .card {
          flex-basis: 40%;
          height: 45vh;
        }
      }
    }
    .fc_wrapper {
      margin-top: -5vh;
    }
    footer {
      // height: 150vh;
      .footerBottom {
        height: 4vh;
      }
    }
  }
}

