        margin-top: -25vh;
        margin-bottom: 25vh;
      }
      .hamburger {
        visibility: visible;
        position: absolute;
        top: 2.5vh;
        left: 2.5vh;
        font-size: 1.25rem;
        color: #fff;
      }
      .hamburgerMenu {
        position: absolute;
        top: 0;
        height: 73vh;
        width: 50%;
        z-index: 8;
        background: #fff;
        flex-direction: column;
        padding: 2vh;
        .returnBtn {
          color: #2E3134;
        }
        .logoWrapper {
