            width: 65%;
            margin-bottom: 2.5vh;
            background-image: url('./images/ifit-logo.svg');
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            bottom: 0;
            left: 2vw;
          }
        }
        .navContent {
          display: flex;
          flex-direction: column;
          .nav-link {
            font-size: 1.5rem;
            margin: 1.5vh 0;
            color: #2E3134;
            &:hover {
              color: #0069D2;
            }
          }
        }
        .navBtn {
          button {
            color: #fff;
            padding: .5rem 1rem;
            margin-top: 2.5vh;
            background: #0069D2;
            border: none;
            border-radius: .15rem;
            font-size: .75rem;
            box-shadow: 2px 2px 10px #575A5C;
            text-transform: uppercase;
          }
        }
      }
    }
    .fc_wrapper {
      height: 140vh;
      .fc_deck {
        flex-wrap: wrap;
        height: 75vh;
        .card {
          margin: 1vh 0;
          flex-basis: 35%;
        }
      }
    }
    footer {
      .footerMenu {
        font-size: .75rem;
        .column {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
      }

      .footerBottom {
        .footerData {
          margin-right: 0;
        }
      }
    }
  }
}
